.wp-block-iowa-aea-theme-important-contacts {
	background-color: #eee;
	width: 100%;
	margin-left: -24%;
	padding: 1rem 3rem 2rem 24%;
	h2 {
		margin: 0 0 1.5rem;
	}

	.btn{
		margin: 1rem;
		padding: 0.5rem 1rem;
		background-color: var(--wp--preset--color--primary);
		color: #fff;
		border: none;
		border-radius: 4px;
		cursor: pointer;

		&:hover {
			background-color: white;
			color: var(--wp--preset--color--primary);
		}
	}

	@media (max-width: 800px) {
		margin-left: -24%;
		padding: 1rem 2rem 2rem 24%;
	}
}

.contacts-block{
	display: flex;
	flex-direction: row;
	gap: 1rem;
	flex-wrap: wrap;
	justify-content: space-between;
}

.contact-item {
	display: grid;
	justify-content: space-between;
	align-items: top;
	border-bottom: 1px solid #eee;
	grid-template-columns: 145px 1fr;
	gap: 1rem;
	background: white;
	border-radius: 19px 19px 0 0;
	border: 1px solid var(--wp--preset--color--primary);
	border-bottom-width: 15px;
	position: relative;
	overflow: hidden;
	width: fit-content;

	h3{
		margin: 0 0 1rem 0;
		font-size: 1.5rem;
		color: var(--wp--preset--color--primary)
	}

	.staff-img {
		max-height: 20rem;
		margin: 0;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: top;
		}
	}

	p{
		margin: 0.5rem 0;
	}
	.btn {
		position: absolute;
		top: -10px;
		right: -10px;
		
	}

	.contact-info{
		padding: 1rem 2rem 0 0;
	}

	@media (max-width: 600px) {
		grid-template-columns: 1fr;
		text-align: center;

		.staff-img {
			max-height: none;
		}

		.contact-info{
			padding: 1rem 1rem 0 1rem;
		}
	}
}
